import { S as SESSION_COOKIE_NAME } from "../../chunks/constants.js";
import { c as createSessionByEmail } from "../../chunks/auth.js";
import { f as fail, r as redirect } from "../../chunks/index.js";
const actions = {
  signin: async ({ request, cookies }) => {
    const formData = await request.formData();
    const password = formData.get("password");
    let email = formData.get("email");
    if (!email.endsWith("@up.edu.ph")) {
      email = `${email}@up.edu.ph`;
    }
    try {
      const sessionCreationResult = await createSessionByEmail(email, password);
      cookies.set(SESSION_COOKIE_NAME, sessionCreationResult.id, {
        path: "/",
        httpOnly: false,
        sameSite: "lax",
        secure: false,
        maxAge: 60 * 60 * 12
      });
    } catch (error) {
      console.error("Error: ", error.message);
      return fail(500, {
        email,
        password,
        message: error.message
      });
    }
    throw redirect(303, "/dashboard");
  }
};
export {
  actions
};
